.container .progressbar-container {
  background-color: gray;
  width: 100%;
  height: 50px;
  border: 1px solid #fff;
  border-radius: 10px;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  overflow: hidden;
  cursor: pointer;
  position: absolute;
}
.container .progressbar-container .progressbar-complete {
  position: absolute;
  right: 0;
  bottom: 0;
  height: 100%;
  background-color: #17a2b8;
  border-radius: 10px;
  -webkit-animation: g 2500ms infinite ease-in-out;
          animation: g 2500ms infinite ease-in-out;
  z-index: 2;
}
.container .progressbar-container .progressbar-complete .progressbar-liquid {
  /* z-index: 1; */
  width: 70px;
  height: 70px;
  -webkit-animation: g 2500ms infinite ease-in-out,
    r 3000ms infinite cubic-bezier(0.5, 0.5, 0.5, 0.5);
          animation: g 2500ms infinite ease-in-out,
    r 3000ms infinite cubic-bezier(0.5, 0.5, 0.5, 0.5);
  position: absolute;
  left: -7px;
  top: -10px;
  background-color: #17a2b8;
  border-radius: 40%;
}
.container .progressbar-container .progress {
  z-index: 2;
  color: white;
  background-color: transparent;
  overflow: visible;
  font-size: 20px;
  -webkit-animation: 3s ease-in-out;
          animation: 3s ease-in-out;
  -webkit-animation-fill-mode: both;
          animation-fill-mode: both;
}
.container .disabled {
  opacity: 0.5;
  cursor: auto;
}

@-webkit-keyframes g {
  0% {
    background-color: #17a2b8;
  }
  50% {
    background-color: #17a2b8;
  }
  100% {
    background-color: #17a2b8;
  }
}

